<template>
  <div>
    <v-card>
      <v-expansion-panel :value="[true]" expand  class="expansion-pl">
        <v-expansion-panel-content>
          <div slot="header">
            <div class="background-triangle-small"> 
              <v-icon size="18" color="white">star_rate</v-icon> 
            </div>
            <span>Thng tin thanh ton</span>
          </div>
          <v-card class="">
            <v-card-text class="px-0 py-0">
              <v-layout wrap>
                <v-flex xs12 sm2>
                  <v-subheader class="pl-0 text-right">L ph: </v-subheader>
                </v-flex>
                <v-flex xs12 sm2>
                  <p class="pt-2 mb-0">{{payments.feeAmount ? currency(payments.feeAmount.toString().replace(/\./g, '')) : 0}} &nbsp;&nbsp; vn</p>
                </v-flex>
                <v-flex xs12 sm2 class="pt-1">
                  <v-subheader class="pl-0 text-right">Ph: </v-subheader>
                </v-flex>
                <v-flex xs12 sm3 class="pt-1">
                  <p class="pt-2 mb-0">{{payments.serviceAmount ? currency(payments.serviceAmount.toString().replace(/\./g, '')) : 0}} &nbsp;&nbsp; vn</p>
                </v-flex>
                <v-flex xs12 sm3></v-flex>
                <!--  -->
                <v-flex xs12 sm2 v-if="(dossierDetail.viaPortal === 2 || dossierDetail.viaPortal === '2') && payments.shipAmount !== 0">
                  <v-subheader class="pl-0 text-right">Ph chuyn pht: </v-subheader>
                </v-flex>
                <v-flex xs12 sm2 v-if="(dossierDetail.viaPortal === 2 || dossierDetail.viaPortal === '2') && payments.shipAmount !== 0">
                  <p class="pt-2 mb-0">{{payments.shipAmount ? currency(payments.shipAmount.toString().replace(/\./g, '')) : 0}} &nbsp;&nbsp; vn</p>
                </v-flex>
                <v-flex xs12 sm8 v-if="(dossierDetail.viaPortal === 2 || dossierDetail.viaPortal === '2') && payments.shipAmount !== 0"></v-flex>
                <!--  -->
                <v-flex xs12 sm2>
                  <v-subheader class="pl-0 text-right"><span class="text-bold">Tng: </span></v-subheader>
                </v-flex>
                <v-flex xs12 sm3 style="padding-top:7px">
                  <span>{{payments.paymentAmount ? currency(payments.paymentAmount.toString().replace(/\./g, '')) : 0}} &nbsp;&nbsp; vn</span>
                </v-flex>
                <v-flex xs12 sm7></v-flex>
                <!--  -->
                <v-flex style="width:100px" class="my-0 pl-3 py-1"><span class="red--text">* </span>&nbsp;Ghi ch:</v-flex>
                <v-flex style="width:calc(100% - 100px)">
                  <p class="px-2 my-0 py-1">
                    {{ePaymentProfile['paymentNote'] ? ePaymentProfile['paymentNote'] : ''}} &nbsp;&nbsp;
                  </p>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>
      <v-expansion-panel :value="[true]" expand class="expansion-pl">
        <v-expansion-panel-content>
          <div slot="header">
            <div class="background-triangle-small"> 
              <v-icon size="18" color="white">star_rate</v-icon> 
            </div>
            <span>Tnh trng thanh ton</span>
          </div>
          <v-card class="">
            <v-card-text class="px-0 py-0">
              <v-layout wrap>
                <v-flex xs12 sm2>
                  <v-subheader class="pl-0 text-right">Trng thi: </v-subheader>
                </v-flex>
                <v-flex xs12 sm3>
                  <p class="pt-2 mb-0">{{getPaymentStatus(payments.paymentStatus)}}</p>
                </v-flex>
                <v-flex xs12 sm7></v-flex>
                <v-layout wrap v-if="doneVTpay">
                  <v-flex xs12 sm2>
                    <v-subheader class="pl-0 text-right">Ngi lm th tc thanh ton: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm3>
                    <p class="pt-2 mb-0"> thanh ton qua ViettelPay
                      <a class="ml-2" href="javascript:;" @click=""
                        style="color: #001fff;text-decoration: underline;font-style: italic;">
                        chi tit
                      </a>
                    </p>
                  </v-flex>
                  <v-flex xs12 sm7></v-flex>
                </v-layout>
                <v-layout wrap v-if="Number(payments.paymentStatus) >= 3">
                  <v-flex xs12 sm2>
                    <v-subheader class="pl-0 text-right">Hnh thc thanh ton: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm3>
                    <p class="pt-2 mb-0">{{payments['paymentMethod']}}</p>
                  </v-flex>
                  <v-flex xs12 sm7></v-flex>
                  <v-flex xs12 sm2 v-if="paymentFile && payments['paymentMethod'] === 'Chuyn khoan'">
                    <v-subheader class="pl-0 text-right">Chng t km theo: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm10 v-if="paymentFile && payments['paymentMethod'] === 'Chuyn khoan'" >
                    <span v-on:click.stop="viewFile()" style="cursor: pointer;">
                      <v-icon>attach_file</v-icon>
                      {{paymentFileName}}
                    </span>
                    <v-tooltip top>
                      <v-btn icon ripple slot="activator" v-on:click.stop="downloadPaymentFile(item)" class="mx-0 my-0">
                        <v-icon style="color: #0d71bb;" size="16" color="primary">save_alt</v-icon>
                      </v-btn>
                      <span>Ti xung</span>
                    </v-tooltip>
                  </v-flex>
                  <!-- thng tin tra cu keypay -->
                  <v-flex xs12 sm2 v-if="payments['paymentMethod'] === 'Keypay'">
                    <v-subheader class="pl-0 text-right">M giao dch: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm3 v-if="payments['paymentMethod'] === 'Keypay'">
                    <p class="pt-2 mb-0">{{transId}}</p>
                  </v-flex>
                  <v-flex xs12 sm7 v-if="payments['paymentMethod'] === 'Keypay'"></v-flex>
                  <v-flex xs12 sm2 v-if="payments['paymentMethod'] === 'Keypay'">
                    <v-subheader class="pl-0 text-right">M n hng: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm3 v-if="payments['paymentMethod'] === 'Keypay'">
                    <p class="pt-2 mb-0">{{goodCode}}</p>
                  </v-flex>
                  <v-flex xs12 sm7 v-if="payments['paymentMethod'] === 'Keypay'"></v-flex>
                  <!-- end -->

                  <!-- bin lai KeyPayDVCQG -->
                  <v-flex xs12 v-if="payments['paymentMethod'] === 'KeyPayDVCQG'">

                  </v-flex>

                  <!-- end -->
                </v-layout>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>
      <!--  -->
      <v-expansion-panel :value="[true]" expand  class="expansion-pl" v-if="Number(payments.paymentStatus) >= 3">
        <v-expansion-panel-content>
          <div slot="header">
            <div class="background-triangle-small"> 
              <v-icon size="18" color="white">star_rate</v-icon> 
            </div>
            <span>Ha n thanh ton</span>
          </div>
          <v-card class="pl-2">
            <v-card-text class="px-0 py-0">
              <v-layout wrap>
                <v-flex xs12 sm2>
                  <v-subheader class="pl-0 text-right">Mu s ha n : </v-subheader>
                </v-flex>
                <v-flex xs12 sm3>
                  <p class="pt-2 mb-0">{{eInvoiceInfo && eInvoiceInfo['supplierTaxCode'] ? eInvoiceInfo['invoiceTemplateNo'] : payments.invoiceTemplateNo}}</p>
                </v-flex>
                <v-flex xs12 sm2>
                  <v-subheader class="pl-0 text-right">S ha n trn h thng : </v-subheader>
                </v-flex>
                <v-flex xs12 sm3>
                  <!-- <p class="pt-2 mb-0">{{getEinvoiceNo(payments.einvoice)}}</p> -->
                  <p class="pt-2 mb-0">{{eInvoiceInfo && eInvoiceInfo['supplierTaxCode'] ? eInvoiceInfo['invoiceNo'] : payments.invoiceNo}}</p>
                </v-flex>
                <v-flex xs12 sm2></v-flex>
                <!-- Ha n in t Vietel -->
                <v-flex xs12 v-if="eInvoiceInfo && eInvoiceInfo['supplierTaxCode']">
                  <v-layout wrap>
                    <v-flex xs12 sm2>
                      <v-subheader class="pl-0 text-right">M s thu: </v-subheader>
                    </v-flex>
                    <v-flex xs12 sm3>
                      <p class="pt-2 mb-0">{{eInvoiceInfo['supplierTaxCode']}}</p>
                    </v-flex>
                    <v-flex xs12 sm2>
                      <v-subheader class="pl-0 text-right">M b mt tra cu ha n: </v-subheader>
                    </v-flex>
                    <v-flex xs12 sm3>
                      <p class="pt-2 mb-0">{{eInvoiceInfo['reservationCode']}}</p>
                    </v-flex>
                    <v-flex xs12 sm2></v-flex>
                  </v-layout>
                </v-flex>
                
                <v-flex xs12 v-else>
                  <v-layout wrap>
                    <v-flex xs12 sm2>
                      <v-subheader class="pl-0 text-right">M tra cu trn h thng : </v-subheader>
                    </v-flex>
                    <v-flex xs12 sm3>
                      <p class="pt-2 mb-0">{{getEinvoiceCode(payments.einvoice)}}</p>
                    </v-flex>
                    <v-flex xs12 sm7></v-flex>
                  </v-layout>
                </v-flex>
                <!--  -->
                <v-flex xs12 sm12 class="ml-3 mb-3">
                  <v-btn color="primary" @click="tracuuhoadon()">
                    <v-icon>search</v-icon> 
                    Tra cu ha n
                  </v-btn>
                  <!-- <v-btn v-if="originality === 3" class="ml-3" color="primary" @click="printPay()"> -->
                  <v-btn class="ml-3" color="primary" @click="printPay()">
                    <v-icon>print</v-icon> &nbsp;
                    In bin lai
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-card>
    <v-dialog v-model="dialogPDF" max-width="900" transition="fade-transition" style="overflow: hidden;">
      <v-card>
        <v-toolbar dark color="primary">
          <v-toolbar-title v-if="!activePrintPay">File nh km</v-toolbar-title>
          <v-toolbar-title v-else>In bin lai thanh ton</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon dark @click.native="dialogPDF = false">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <div v-if="dialogPDFLoading" style="
          min-height: 600px;
          text-align: center;
          margin: auto;
          padding: 25%;
        ">
          <v-progress-circular
            :size="100"
            :width="1"
            color="primary"
            indeterminate
          ></v-progress-circular>
        </div>
        <iframe v-show="!dialogPDFLoading" id="dialogPaymentPreview" src="" type="application/pdf" width="100%" height="100%" style="overflow: auto;min-height: 600px;" frameborder="0">
        </iframe>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

import Vue from 'vue'
import axios from 'axios'
export default {
  props: {
    payments: {
      type: Object,
      default: () => {}
    },
    dossierDetail: {
      type: Object,
      default: () => {}
    }
  },
  components: {},
  data: () => ({
    loading: true,
    loadingAction: false,
    paymentFile: '',
    dialogPDF: false,
    dialogPDFLoading: true,
    activePrintPay: false,
    transId: '',
    goodCode: '',
    doneVTpay: false
  }),
  computed: {
    paymentFileName () {
      return this.$store.getters.getPaymentFileName
    },
    originality () {
      var vm = this
      return vm.getOriginality()
    },
    ePaymentProfile () {
      let vm = this
      return vm.getEPaymentProfile(vm.payments['epaymentProfile'])
    },
    eInvoiceInfo () {
      let vm = this
      try {
        return JSON.parse(vm.payments['einvoice'])
      } catch (error) {
        console.log('Khng c thng tin ha n - einvoice')
        return ''
      }
    }
  },
  created () {
    let vm = this
    vm.$nextTick(function () {
      var vm = this
    })
  },
  watch: {
    dossierDetail (val) {
      var vm = this
      vm.getPaymentFiles()
    },
    payments (val) {
      // ly thng tin tra cu trn keypay
      let vm = this
      // if (!vm.paymentFile && val['paymentMethod'] === 'Chuyn khoan') {
      //   vm.payments.paymentMethod = 'Keypay'
      // }
      let paymentProfile = vm.getEPaymentProfile(val.epaymentProfile)
      // keypay
      if (paymentProfile && paymentProfile['keypayUrl']) {
        vm.transId = paymentProfile['keypayUrl'].split('&').filter(function (item) {return item.indexOf('merchant_trans_id') >= 0})[0].split('=')[1]
        vm.goodCode = paymentProfile['keypayUrl'].split('&').filter(function (item) {return item.indexOf('good_code') >= 0})[0].split('=')[1]
      }
      // viettelPay
      if (paymentProfile && paymentProfile.genQRCode && String(val.paymentStatus) !== '3' && String(val.paymentStatus) !== '5') {
        let filter = {
          billcode: paymentProfile.genQRCode.BILLCODE,
          order_id: paymentProfile.genQRCode.ORDER_ID
        }
        vm.$store.dispatch('getVtPayStatus', filter).then(result => {
          // vm.doneVTpay = true
        }).catch(function(){})

      }
    }
  },
  methods: {
    getPaymentFiles () {
      let vm = this
      let filter = vm.dossierDetail
      vm.$store.dispatch('getPaymentFiles', filter).then(result => {
        vm.paymentFile = result
      }).catch(function(){})
    },
    currency (value) {
      if (value) {
        let moneyCur = (value / 1).toFixed(0).replace('.', ',')
        return moneyCur.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')
      }
      return ''
    },
    viewFile () {
      let vm = this
      vm.activePrintPay = false
      vm.dialogPDFLoading = true
      vm.dialogPDF = true
      let filter = {
        dossierId: vm.dossierDetail.dossierId,
        referenceUid: vm.dossierDetail.referenceUid
      }
      vm.$store.dispatch('viewPaymentFile', filter).then(result => {
        vm.dialogPDFLoading = false
        document.getElementById('dialogPaymentPreview').src = result
      }).catch(function(){})
    },
    getEPaymentProfile (paymentProfile) {
      if (paymentProfile) {
        try {
          JSON.parse(paymentProfile)
          return JSON.parse(paymentProfile)
        } catch (e) {
          return ''
        }
      } else {
        return ''
      }
    },
    downloadPaymentFile (item) {
      let vm = this
      let url = '/o/rest/v2/dossiers/' + vm.dossierDetail.dossierId + '/payments/' + vm.dossierDetail.referenceUid + '/confirmfile'
      window.open(url)
    },
    printPay () {
      let vm = this
      vm.activePrintPay = true
      let filter = {
        dossierId: vm.dossierDetail.dossierId,
        referenceUid: vm.payments.referenceUid
      }
      vm.dialogPDFLoading = true
      vm.dialogPDF = true
      if (vm.payments['paymentMethod'] === 'KeyPayDVCQG') {
        let confirmPayload = vm.payments.hasOwnProperty('confirmPayload') ? vm.getEPaymentProfile(vm.payments['confirmPayload']) : ''
        vm.dialogPDFLoading = false
        let url = confirmPayload ? confirmPayload['url_invoice'] : ''
        if (url) {
          window.open(url, "_blank")
        } else {
          alert('Khng c bin lai')
        }
        
      } else if (vm.payments['paymentMethod'] === 'PayPlatDVCQG') {
        // let param = {
        //   headers: {
        //     groupId: window.themeDisplay.getScopeGroupId()
        //   },
        //   responseType: 'blob'
        // }
        // axios.get('/o/rest/v2/dossiers/'+ filter.dossierId + '/payments/' + filter.referenceUid + '/invoice', param).then(function (response) {
        //   vm.dialogPDFLoading = false
        //   let serializable = response.data
        //   let file = window.URL.createObjectURL(serializable)
        //   document.getElementById('dialogPaymentPreview').src = file
        // }).catch(function (error) {
        //   vm.dialogPDFLoading = false
        // })
        vm.dialogPDFLoading = false
        vm.dialogPDF = false
        let url = '/o/rest/v2/dossiers/'+ filter.dossierId + '/payments/' + filter.referenceUid + '/invoice'
        window.open(url, "_blank")
      } else {
        vm.$store.dispatch('printPay', filter).then(function (result) {
          vm.dialogPDFLoading = false
          document.getElementById('dialogPaymentPreview').src = result
        }).catch(function(){})
      }
    },
    getEinvoiceNo (string) {
      if (string && string.indexOf('#') >= 0) {
        let eInvoiceNo = string.split('#')[1] ? string.split('#')[1] : ''
        return eInvoiceNo.indexOf('}') < 0 && eInvoiceNo.indexOf('"') < 0 ? eInvoiceNo : ''
      } else {
        return ''
      }
    },
    getEinvoiceCode (string) {
      if (string && string.indexOf('#') >= 0) {
        let eInvoiceCode = string.split('#')[3] ? string.split('#')[3].split(',')[0].replace('"', '') : ''
        return eInvoiceCode
      } else {
        return ''
      }
    },
    tracuuhoadon () {
      let vm = this
      let urlRedirect
      if (vm.eInvoiceInfo && vm.eInvoiceInfo['supplierTaxCode']) {
        urlRedirect = vm.eInvoiceInfo['sInvoiceUrl']
      } else {
        urlRedirect = 'http://tracuu.cmcsoft.com'
      }
      window.open(urlRedirect, '_blank')
    },
    checkVTpay () {
      let vm = this
      let paymentProfile = vm.getEPaymentProfile(vm.payments.epaymentProfile)
      let filter = {
        billcode: paymentProfile.genQRCode.BILLCODE,
        order_id: paymentProfile.genQRCode.ORDER_ID
      }
      vm.$store.dispatch('getVtPayStatus', filter).then(result => {

      }).catch(function(){})
    },
    getPaymentStatus (code) {
      if (code === 1 || code === '1') {
        return 'Yu cu np tin tm ng '
      } else if (code === 2 || code === '2') {
        return 'Yu cu quyt ton ph'
      } else if (code === 3 || code === '3') {
        return 'Bo  np ph trc tuyn'
      } else if (code === 4 || code === '4') {
        return 'Yu cu np li ph trc tuyn'
      } else if (code === 5 || code === '5') {
        return ' hon thnh thu ph'
      } else {
        return ''
      }
    }
  }
}
</script>
